<template>
 
<div class="outer">
  <div class="middle">
    <div class="inner">
      <div class="container aboutus">
	  <span class="abouttext">
			Every adventure starts with a map. We follow the trails that lead us to places
filled with immense beauty, history and unimaginable atmospheres. <br/><br/>We often
seek to find places that are still yet to be fully explored, aspiring to be those who
had discovered the trails that are now followed by millions. Could we all become
the founders of unexplored places?<br/><br/>
Atlas Trails is a growing collective of explorers searching for those undiscovered
places and the stories they hold. Our purpose is to allow everybody to share all
aspects of the places they explore from all corners of the world, becoming
founders of their own and creating new trails for others to follow too. <br/><br/>Want to share your discovery? Submissions to Atlas Trails will open soon!
In the meantime submit your images and stories via our Instagram page.
		</span>
	  </div>

    </div>
  </div>
</div>

</template>



<script>

  export default {
    name: 'about',
    props: {
      msg: {
        type: String,
        default: 'About'
      }
    },
    data(){
      return {

      }
    },
    methods: {

    },
    computed: {

    }
  }

</script>

<style scoped>

.outer {
    display: table;
    position: absolute;
    height: 85%;
    width: 100%;
}

.middle {
    display: table-cell;
    vertical-align: middle;
}

.inner {
    margin-left: auto;
    margin-right: auto; 
    width: 75%;
}

.aboutus {
	padding: 50px;
	background: rgba(255, 255, 255, 0.75);
	border-radius: 20px;
	font-size: 20px;
	font-family: Raleway;
}

</style>



