// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'
import Home from './components/Home'
import About from './components/About'
//import Shots from './components/Shots'
import Contact from './components/Contact'
import Carousel3d from 'vue-carousel-3d'
import quickMenu from 'vue-quick-menu'

Vue.use(VueRouter);
Vue.use(Carousel3d);
Vue.component(quickMenu.name,quickMenu)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    { path: '/', component: Home },
    { path: '/about', component: About },
    { path: '/contact', component: Contact },
  ]
});

require('../static/css/mycss.css');

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  router,
  data: {
    count:4,
    icons:["fa fa-home","fa fa-info-circle","fa fa-weixin"],
    list:["/","/about","/contact"],
    backgroundColor:'#ffffff',
    color:'#000000',
    position:'bottom-left',
    isOpenNewTab:false
  },
  methods: {

  },
  template: `
    <div id="app">

    <quick-menu :menu-count=3 :icon-class=icons :menu-url-list=list :backgroundColor=backgroundColor :position=position class="menuelement"></quick-menu>

    <!--<ceri-side-nav target=#nav-toggle>
      <router-link to="/"><a class="button button-primary" style="margin-left: 10%;margin-top: 25px; padding-left:100px; padding-right:100px">Home</a></router-link><br/>
      <router-link to="/about"><a class="button button-primary" style="margin-left: 10%; margin-top: 25px; padding-left:100px; padding-right:100px">About</a></router-link><br/>
      <router-link to="/shots"><a class="button button-primary" style="margin-left: 10%; margin-top: 25px; padding-left:100px; padding-right:100px">Shots</a></router-link><br/>
      <router-link to="/contact"><a class="button button-primary" style="margin-left: 10%; margin-top: 25px; padding-left:100px; padding-right:100px">Contact</a></router-link><br/>
    </ceri-side-nav>-->

    <!--
    <expand-ball>
        <span slot="menu"><router-link to="/">Home</router-link></span>
        <span slot="menu">M2</span>
        <span slot="menu">M3</span>
    </expand-ball>
    -->


    <!--
    <div id="navigationbar">
      <router-link to="/"><a class="button" style="margin-left: 10%;margin-top: 25px; padding-left:100px; padding-right:100px">Home</a></router-link>
      <router-link to="/about"><a class="button" style="margin-left: 10%; margin-top: 25px; padding-left:100px; padding-right:100px">About</a></router-link>
      <router-link to="/shots"><a class="button" style="margin-left: 10%; margin-top: 25px; padding-left:100px; padding-right:100px">Shots</a></router-link>
      <router-link to="/contact"><a class="button" style="margin-left: 10%; margin-top: 25px; padding-left:100px; padding-right:100px">Contact</a></router-link>
    </div>
    -->


      <router-view></router-view>

    </div>
  `
}).$mount('#app')



